import React from 'react';
import { Helmet } from 'react-helmet';
import { globalHistory } from "@reach/router";

export default function Metadata(props) {
  return (
      <Helmet>
        <title>{props.Title}</title>
        <meta property="og:title" content={props.Title} />
        <meta property="og:description" content={props.Description} />
        <meta property="og:image" content="https://cdn.bipul.in/bipul.in/images/meta.png"/>
        <meta property="og:url" content={globalHistory.location.href} />
        <meta property='og:type' content='article' />
        <meta property='article:author' content='https://www.facebook.com/TheBipulRaman' />
        <meta property='article:publisher' content='https://www.facebook.com/TheBipulRaman' />
        <meta property='og:section' content="Blog" />
        <meta property='og:site_name' content="Bipul's Web" />
        <meta name="twitter:title" content={props.Title} />
        <meta name="twitter:description" content={props.Description} />
        <meta name="twitter:image" content="https://cdn.bipul.in/bipul.in/images/meta.png" />
        <meta name="twitter:card" content="summary_large_image"></meta>
        <meta name="twitter:site" content="@BipulRaman" />
        <meta name="twitter:creator" content="@BipulRaman" />
      </Helmet>  
  );
}